import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { TranslateService } from '@ngx-translate/core';
import { HttpProvider } from '../../providers/http/http';
import { UtilsProvider } from '../../providers/utils/utils';
import { CommandsProvider } from '../../providers/commands/commands';

/**
 * Generated class for the CreditoMovimientoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-credito-movimiento',
  templateUrl: 'credito-movimiento.html',
})
export class CreditoMovimientoPage {

  private lang : any; 

  public lista2 : Array<string>;
  public creditLst : Array<string>;
  public message: string;
  public creditLimiTxt : string;
  public strHead : string;
  public posicion: number;
  public cardNumber : string;
  public creditLimit : string;
  public nextStatement : string;
  public lastPayment : string;
  public balance : string;
  public available : string;
  public minimumPayment : string;
  public date : string;
  public contadorDetalleC : number = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, private translateService: TranslateService,public http: HttpProvider,
              public utils: UtilsProvider, public command: CommandsProvider) {
        
    this.storage.get('possicion').then((val) => {  
      this.posicion = val;
   }); 

   this.storage.get('creditLst').then((val) => { 
     if (val != null && val != "") { 
      this.creditLst = val;
      }
    });
  }

  ionViewDidLoad() {
    this.storage.get('CClista2').then((val) => { if (val != null && val != "") { this.lista2 = val; }});
    this.storage.get('strHeadCC').then((val) => { if (val != null && val != "") { this.strHead = val; this.cardNumber = this.strHead[0]; this.balance = this.strHead[5]; this.available = this.strHead[8]; } });
    this.lang = this.translateService.getDefaultLang();
    this.utils.getLanguage();  
    this.utils.getSessionId();
    
  }

  ionViewDidEnter() {
    
    if ((this.lista2 == null || this.lista2 == undefined) && (this.strHead == null || this.strHead == undefined)) {
        this.detalleTarjeta();
    }
  }

  movimi(){
    this.navCtrl.push("FiltroPage", { flagFiltro: 3 });
  }

  private detalleTarjeta() {   
    
    //var command = 'E2LSCR89L'; 
    var command :string[] =  this.utils.getCommands(this.command.E2LSCR89L);
    this.storage.get('contadorDetalleCuenta').then((val) => {  
      if(val != null){
        this.contadorDetalleC = val;
      }        
    });

    var param = command[0] + '&CH=' + this.utils.channel + '&LNG=' + this.utils.getLanguage() + '&SID=' + this.utils.getSessionId() + '&E2EW=%20' + this.utils.separator + 'E2TARJ' + this.utils.separator +'0'+ this.utils.separator + 'E2SECR61P' + this.utils.separator + '0' + this.utils.separator + this.posicion  + this.utils.separator;

    this.http.callServer(param).then(res => {
      this.message = res.message;

      if (this.message != '' && typeof this.message !== "undefined") {
        //this.utils.alertServidor(this.message);
        if(this.message == "No data available for your request" && this.contadorDetalleC == 0){
          this.utils.alertServidor(this.message);
          this.storage.set("contadorDetalleCuenta", 1);
        }
        console.log("Respuesta enviada del SERVIDOR: "+this.message);

      } else {
        this.lista2 = res.lista2; 
        this.strHead = res.strHead;
        this.cardNumber = this.strHead[0];
        this.creditLimit = this.strHead[3];
        this.nextStatement = this.strHead[4];
        this.lastPayment = this.strHead[12];
        this.balance = this.strHead[5];
        this.available = this.strHead[8];
        this.minimumPayment  = this.strHead[7];
        this.date = this.strHead[11];
        
        //Se guarda en sesion todas las listas y asi no volver hacer peticiones la servidor
        this.storage.set("CClista2", res.lista2);
        this.storage.set("strHeadCC", res.strHead);
      }
    },
      error => {
        console.log(error);
      });
  }

  mas(){
    this.navCtrl.push("");
  }

   //Tabla 1
   mostrar1() {
    document.getElementById("caja").style.display = "block";
  }

  ocultar1() {
    document.getElementById("caja").style.display = "none";
  }

  todo() {

    const caja = document.getElementById("caja");

    if (caja.style.display == "none") {
      this.mostrar1();
    }
    else {
      this.ocultar1();
    }
  }

  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }
  
}



